import React from 'react'
import {useDispatch, useSelector, shallowEqual} from 'react-redux';
import {getThisFormMBFElements} from './UsefulFunctions';
import "./SocialInfo.css";

function SocialMedia({values}) {
  const cf = useSelector(state=>state.cf, shallowEqual);
  const dispatch = useDispatch();
  
  let socialMedias = ['Website', 'Telegram', 'Twitter', 'Facebook', 'Discord', 'Linkedln', 'Reddit', 'Youtube']
  let necSocials = ['Website', 'Telegram'];

  const handleIndex = (event)=>{
    switch(event.target.name){
        case "NextDot":
            if(validateForm() && dispatch_data()){
              dispatch({type:'nextForm'})
            } 
            break;
        case "PrevDot":
            dispatch({type:'prForm'})
            break;
    }
  }

  const validateForm = ()=>{
    let validated = true;
    let MBFs = getThisFormMBFElements(values);
    let to_be_filled = ""
    for(let i =0; i < MBFs.length; i++){
        if(MBFs[i].value == "" || MBFs[i].value == null){
            MBFs[i].style.boxShadow = '0px 0px 10px red';
            to_be_filled += "\n-" + MBFs[i].id;
            validated = false;
        }
    }
    if(!validated) alert("Please fill this fields: "+to_be_filled);
    return validated;
  }

  const dispatch_data = ()=>{
    let vk = Object.keys(values);
    let finalData = {}
    for(let i = 0; i < vk.length; i++){
        let e = document.getElementById(vk[i]);
        finalData[vk[i]] = e.value;
    }
    dispatch({type:'updateForm', formName : "SocialInfo", value:finalData});
    return true;
  }

  return (
    <div className='SI_mainDiv'>
      <div className='SI_formContainer'>
        {Array(socialMedias.length).fill().map((_, i)=>{
          return <input name={necSocials.includes(socialMedias[i])? 'MBF' : ''} id={socialMedias[i]} placeholder={socialMedias[i]} defaultValue={values[socialMedias[i]]}></input>
        })}
        <div style={{display:'flex', justifyContent:'flex-end'}}>
          <button className='SI_Button' name='PrevDot' onClick={handleIndex}>Back</button>
        </div>
        <div>
          <button className='SI_Button' name='NextDot' id='SocialInfo' onClick={handleIndex}>Next</button>
        </div>
      </div>
    </div>
  )
}

export default SocialMedia