import React, {useState, useEffect, useRef} from 'react';
import './AddCoinSlider.css';
import TokenInfo from './AddCoin_Forms/TokenInfo';
import EventInfo from './AddCoin_Forms/EventInfo';
import ProjectInfo from './AddCoin_Forms/ProjectInfo';
import SocialMedia from './AddCoin_Forms/SocialMedia';
import Plans from './AddCoin_Forms/Plans';
import Approve from './AddCoin_Forms/Approve';
import Payment from './AddCoin_Forms/Payment';
import {useDispatch, useSelector, shallowEqual} from 'react-redux';

function AddCoinSlider() {
    const steps = ["Token Info", "Event Info", "Project Info", "Social Media", "Plans", "Payment", "Approve"];
    const values = useSelector(state=>state.form, shallowEqual);
    const dispatch = useDispatch();
    const cf = useSelector(state=>state.cf, shallowEqual);
    const passedSteps = useRef(cf);

    const sections = {
        "Token Info" : <TokenInfo values={values['TokenInfo']}></TokenInfo>,
        "Event Info" : <EventInfo values={values['EventInfo']}></EventInfo>,
        "Project Info" : <ProjectInfo values={values['projectInfo']}></ProjectInfo>,
        "Social Media" : <SocialMedia values={values['SocialInfo']}></SocialMedia>,
        "Plans" : <Plans></Plans>,
        "Payment" : <Payment values={values['Payment']} plan={values['Plans'].name} payment={values['Plans'].value}></Payment>,
        "Approve" : <Approve values={values['Approve']}></Approve>
    }
    const [currentForm, setCurrentForm] = useState(sections[steps[cf]]);


    const [filledStep, setFilledStep] = useState('0%');
    const [dotIndex, setDotIndex] = useState(0);

    const stepClicked = (event)=>{
        if(steps.indexOf(event.currentTarget.id) > passedSteps.current) return;
        dispatch({type:'setIndex', value:steps.indexOf(event.currentTarget.id)})
        colorizeDots(steps.indexOf(event.currentTarget.id));
    }

    const colorizeDots = (di)=>{
        let fill = di * (100 / (steps.length - 1));
        setFilledStep(fill + "%");
        for(let i = 0; i < steps.length; i++){
            document.getElementById(steps[i]).style.backgroundColor = (i <= di)? 'blue' : 'black';
        }
    }

    useEffect(() => {
        colorizeDots(cf);
        let elements = document.getElementsByName('MBF');
        elements.forEach(i=>{
            i.addEventListener('change', function(){
                if(i.style.boxShadow == "none") return;
                    i.style.boxShadow = "none";
                })
        })
        if(passedSteps.current < cf){
            passedSteps.current = cf;
        }
        setCurrentForm(sections[steps[cf]]);
    }, [cf])
    
    
    return (
    <div className='AddCoinSlider_Main'>
        <div className='AddCoinSlider_Container'>
            <div className='Slider_Steps'>
                    {Array(steps.length).fill().map((_, i)=>{
                        return <div className='Step_container'>
                            <div className='Step_Dot' id={steps[i]} onClick={stepClicked}></div>
                        </div>
                    })}
                </div>
            <div className='Slider_Container'>
                <div className='Slider_Progress'>
                    <div className='Slider_Progress_bar' style={{width:filledStep}}></div>
                </div>
            </div>
        </div>
        <div className='Slider_Steps_Text'>
                {Array(steps.length).fill().map((_, i)=>{
                    return <div className='Step_container'>
                        <div className='Step_Text'>
                            <span>{steps[i]}</span>
                        </div>
                    </div>
                })}
        </div>

        <div className='Forms'>
            {currentForm}
        </div>
    </div>
  )
}

export default AddCoinSlider;