import React, {useState, useEffect} from 'react';
import "./TokenInfo.css"
import {useDispatch, useSelector, shallowEqual, Provider} from 'react-redux';
import {getThisFormMBFElements} from './UsefulFunctions';

function TokenInfo({values}) {
    const dispatch = useDispatch();
    const [logo, setLogo] = useState(values.LogoImage);
    
    const LogoSelected = ()=>{
        let Address = document.getElementById('LogoImage').files[0].name;
        setLogo("/" + Address);
    }
    
    const selectLogoClick = (event)=>{
        event.target.nextElementSibling.click();
    }
    
    const Selects = {
        "Chain" : ["BSC", "Ethereum", "Polygon", "Avalanche", "Cronos", "Tron", "Algorand", "Solana"],
        "Events" : ["Select Event", "Launch After Presale", "Fair Launch", "Presale", "Private Presale"],
        }

    const handleIndex = (event)=>{
        switch(event.target.name){
            case "NextDot":
                if(validateForm() == true && dispatch_data() == true){
                    dispatch({type:'nextForm'})
                }
                break;
            case "PrevDot":
                dispatch({type:'prForm'})
                break;
        }
    }

    const validateForm = ()=>{
        if(logo == ''){
            alert('please select a logo first!')
            return false;
        }
        let validated = true;
        let MBFs = getThisFormMBFElements(values);
        let to_be_filled = ""
        for(let i =0; i < MBFs.length; i++){
            if(MBFs[i].value == "" || MBFs[i].value == null){
                MBFs[i].style.boxShadow = '0px 0px 10px red';
                to_be_filled += "\n-" + MBFs[i].id;
                validated = false;
            }
        }
        if(!validated) alert("Please fill this fields: "+to_be_filled);
        return validated;
    }

    const dispatch_data = ()=>{
        let vk = Object.keys(values);
        let finalData = {};
        for(let i = 0; i < vk.length; i++){
            let e = document.getElementById(vk[i]);
            finalData[vk[i]] = e.value;
        }
        finalData['LogoImage'] = logo;
        dispatch({type:'updateForm', formName : "TokenInfo", value:finalData});
        return true;
    }
    
  return (
      <div className='TI_mainDiv'>
        <div style={{display:'flex', width:'100%', justifyContent:'space-between'}}>
            <div>
                <div className='TI_Logo' onClick={selectLogoClick} style={{backgroundImage:`url(${process.env.PUBLIC_URL + logo})`}}>
                    { logo == '' && <span>Logo</span>  }
                </div>
                <input type={'file'} hidden accept='image/*' id='LogoImage' onChange={LogoSelected}></input>
            </div>
            <div className='TI_Fields'>
                <input placeholder='name' defaultValue={values.name} id={'name'} name={'MBF'}></input>
                <input placeholder='Symbol' defaultValue={values.symbol} id={'symbol'} name={'MBF'}></input>
                <input placeholder='Total Supply' defaultValue={values.totalSupply} id={'totalSupply'}></input>
                <input placeholder='Decimals' defaultValue={values.decimals} id={'decimals'}></input>
            </div>
            <div className='TI_Fields'>
                <input placeholder='Contract Address' defaultValue={values.contractAddress} id={'contractAddress'}></input>
                <select id='chain' defaultValue={values.chain} name={'MBF'}>
                    {Array(Selects['Chain'].length).fill().map((_, i)=>{
                        return <option value={Selects['Chain'][i]} key={'chainOption'+i}>{Selects['Chain'][i]}</option>
                    })}
                </select>
                <input placeholder='Audit Link (github)' defaultValue={values.auditLink} id={'auditLink'}></input>
                <input placeholder='Chart Link' defaultValue={values.chartLink} id={'chartLink'}></input>
            </div>
            <div className='TI_Fields'>
                <input placeholder='Presale %' defaultValue={values.presale} id={'presale'}></input>
                <input placeholder='Locked %' defaultValue={values.locked} id={'locked'}></input>
                <input placeholder='Burn %' defaultValue={values.burn} id={'burn'}></input>
                <input placeholder='Team %' defaultValue={values.team} id={'team'}></input>
                <input placeholder='Liquidity %' defaultValue={values.lp} id={'lp'}></input>
            </div>
        </div>
        <div style={{paddingRight:'20px', height:'100%'}}>
            <button className='TI_Button' name='NextDot' id='TokenInfo' onClick={handleIndex}>Next</button>
        </div>
    </div>
  )
}

export default TokenInfo