import React from 'react';
import "./ProjectInfo.css";
import {useDispatch, useSelector, shallowEqual} from 'react-redux';
import {getThisFormMBFElements} from './UsefulFunctions';

function ProjectInfo({values}) {
    const cf = useSelector(state=>state.cf, shallowEqual);
    const dispatch = useDispatch();
    const categories = ["P2E", "Meme", "Community", "Staking", "Defi", "NFT", "DEX", "Exchange", "Lottery"];
    
    const handleIndex = (event)=>{
        switch(event.target.name){
            case "NextDot":
                if(validateForm() == true && dispatch_data()){
                    dispatch({type:'nextForm'})
                }
                break;
            case "PrevDot":
                dispatch({type:'prForm'})
                break;
        }
    }

    const validateForm = ()=>{
        let validated = true;
        let MBFs = getThisFormMBFElements(values);
        let to_be_filled = ""
        for(let i =0; i < MBFs.length; i++){
            if(MBFs[i].value == "" || MBFs[i].value == null){
                MBFs[i].style.boxShadow = '0px 0px 10px red';
                to_be_filled += "\n-" + MBFs[i].id;
                validated = false;
            }
        }
        if(!validated) alert("Please fill this fields: "+to_be_filled);
        return validated;
    }

    const dispatch_data = ()=>{
        let vk = Object.keys(values);
        let finalData = {}
        for(let i = 0; i < vk.length; i++){
            let e = document.getElementById(vk[i]);
            if(vk[i] == "category"){
                finalData[vk[i]] = e.options[e.selectedIndex].text;
            }else{
                finalData[vk[i]] = e.value;
            }
        }
        dispatch({type:'updateForm', formName : "projectInfo", value:finalData});
        return true;
    }
    
  return (
    <div className='PI_mainDiv'>
        <div className='PI_formContainer'>
            <select name='MBF' defaultValue={values.category} id={'category'} style={{marginBottom:'20px'}}>
                {Array(categories.length).fill().map((_, i)=>{
                    return <option value={categories[i]}>{categories[i]}</option>
                })}
            </select>
            <input name='MBF' placeholder='Website' defaultValue={values.website} id='website' style={{marginBottom:'20px'}}></input>
            <input placeholder='KYC Certificate Link' defaultValue={values.kyc_link} id='kyc_link' style={{marginBottom:'20px'}}></input>
        </div>
        <div>
            <textarea className='PI_textArea' placeholder={'tell us more about your project(this will be your project description)...'}
             defaultValue={values.projectDetails} id='projectDetails' name='MBF'></textarea>
        </div>
            <div style={{marginRight:'10px'}}>
                <button className='PI_Buttons' name='NextDot' id='ProjectInfo' onClick={handleIndex}>Next</button>
                <button className='PI_Buttons' style={{marginTop:'10px'}} name='PrevDot' onClick={handleIndex}>Back</button>
            </div>
    </div>
  )
}

export default ProjectInfo