import React from 'react'
import {useDispatch, useSelector, shallowEqual} from 'react-redux';
import "./Payment.css";

function Payment({values, plan, payment}) {
  const cf = useSelector(state=>state.cf, shallowEqual);
  const dispatch = useDispatch();

  const handleIndex = (event)=>{
    switch(event.target.name){
        case "NextDot":
            if(validateForm() == true && dispatch_data()){
              dispatch({type:'nextForm'})
            } 
            break;
        case "PrevDot":
            dispatch({type:'prForm'})
            break;
    }
  }

  const validateForm = ()=>{
    let txh_input = document.getElementById('TXH');
    if(txh_input.value == ""){
      alert('please enter the transaction hash!')
      txh_input.style.boxShadow = '0px 0px 10px red';
      return false;
    }
    return true;
  }

  const dispatch_data = ()=>{
    let vk = Object.keys(values);
    let finalData = {}
    for(let i = 0; i < vk.length; i++){
        let e = document.getElementById(vk[i]);
        finalData[vk[i]] = e.value;
    }
    dispatch({type:'updateForm', formName : "Payment", value:finalData});
    return true;
  }

  return (
    <div className='Pay_mainDiv'>
      <span className='Pay_title'>
        Selected Plan: <span style={{color:'white'}}>{plan}</span>
      </span>
      <div className='Pay_details'>
        <span>Send <b style={{color:'gold'}}>{payment} BUSD</b> To: </span>
        <span>{'0x94E32912613555a884a0e9Eb475cFbB20A969B1A'}</span>
        <span>And Paste Transaction Hash Here(only <b style={{color:'gold'}}>BEP20</b> network):</span>
        <input name='MBF' placeholder='Hash' style={{marginTop:'10px', width:'100%'}} defaultValue={values.TXH} id='TXH'></input>
        <div style={{marginTop:'20px', display:'flex'}}>
          <button className='Pay_Button' name='PrevDot' onClick={handleIndex}>Back</button>
          <button className='Pay_Button' name='NextDot' id='Payment' onClick={handleIndex}>Submit</button>
        </div>
      </div>
    </div>
  )
}

export default Payment