import React, {useEffect, useRef, useState} from 'react'
import "./Lottery.css";
import {ReactComponent as Treasure} from "../../components/images/Treasure.svg";

function Lottery() {
  const rendered = useRef(false);
  const [TimerText, setTimerText] = useState("00:00:00");
  let timestamp = 600;
  let delay = 10000;
  const TimeStamp = useRef(timestamp);
  const ended = useRef(false);

  useEffect(()=>{
    if(ended.current == true){
        setTimeout(()=>{
            ended.current = false;
            setTimerText("NaN:NaN:NaN");
            TimeStamp.current = timestamp;
        }, delay);
    }
    if(!rendered.current){
        setInterval(()=>{
            if(ended.current) return;
            TimeStamp.current -= 1;
            let ts = TimeStamp.current;
            let H = Math.floor((ts / 3600 < 10)) ? "0"+Math.floor(ts / 3600) : Math.floor(ts / 3600);
            ts -= H * 3600;
            let M = Math.floor((ts / 60 < 10)) ? "0"+Math.floor(ts / 60) : Math.floor(ts / 60);
            ts -= M * 60;
            ts = ts < 10? "0" + ts : ts;
            if(TimeStamp.current == 0){
                ended.current = true;
            }
            setTimerText(H+":"+M+":"+ts);
        }, 1000);
        rendered.current = true;
    }
  }, [TimerText, ended.current]);

  return (
    <div className='Lo_mainDiv'>
        <div className='Lo_Timer'>
            <span>{TimerText}</span>
        </div>
        {
            ended.current && 
            <div className='Lo_winner'>
                <span style={{fontFamily:'fantasy', color:'gold'}}>Winner:</span>
                <span style={{border:'1px solid #1f1f1f', padding:'5px', marginTop:'5px', borderRadius:'15px'}}>0x94E32912613555a884a0e9Eb475cFbB20A969B1A</span>
            </div>
        }
        <div className='Lo_TreasureLogo'>
            <Treasure></Treasure>
            <span style={{color:'blue', fontFamily:'fantasy'}}>Winner Gets:</span>
            <span style={{color:'white', marginTop:'5px'}}>0$</span>
        </div>
        <div className='Lo_participationDetails'>
            <span>To Participate In Lottery Send <b>3 BUSD(BEP20)</b> To This Contract Address:</span>
            <span style={{color:'rgb(59, 104, 255)', marginTop:'5px'}}>0x94E32912613555a884a0e9Eb475cFbB20A969B1A</span>
            <span>You Will Be Automatically Added To The Pool.</span>
        </div>
        <div className='Lo_check'>
            <input placeholder='Am I In? (Your Address)'></input>
            <button className='Lo_Button'>Check</button>
        </div>
        <div className='Lo_Cancel'>
            <button className='Lo_Button'>Refund</button>
        </div>
    </div>
  )
}

export default Lottery