import React, { useState, useEffect, useRef } from 'react'
import "./EventInfo.css"
import {useDispatch, useSelector, shallowEqual} from 'react-redux';

function EventInfo({values}) {
  const cf = useSelector(state=>state.cf, shallowEqual);
  const SP = useRef("Current Phase");
  const dispatch = useDispatch();
  
  const Selects = {
    "Chain" : ["BSC", "Ethereum", "Polygon", "Avalanche", "Cronos", "Tron", "Algorand", "Solana"],
    "Events" : ["Current Phase", "Whitelist", "Presale", "Launch"],
    }
  
  const conditions = [
    "If current event is live, just check 'is Live' check box and leave 'start' field empty.",
    "If your token is already launched and tradeable, fill 'launch' section and check 'is Live' checkbox.",
  ]

  const mustBefilled = {
    "launch_box" : ["launch_time", "buyTax", "sellTax"],
    "presale_box" : ["presale_time", "hardCap", "softCap"],
    "whitelist_box" : ["whitelist_start", "whitelist_end", "whitelist_link"]
  }

  const enableDisableSections = (event)=>{
      let name = event.target.name;
      if(name != null){
        let bc = event.target.checked;
        document.getElementById(name+"_timeline_dot").style.backgroundColor = bc? "blue" : "black";
        document.getElementById(name+"_timeline_text").style.color = bc? "white" : "#6c6c6c";
        document.getElementById(name + "_section").style.opacity = bc? "1" : "0.5";
        document.getElementById(name + "_section").style.pointerEvents = bc? "all" : "none";
      }
  }

  const handleIndex = (event)=>{
    switch(event.target.name){
        case "NextDot":
            if(validateForm() && dispatch_data()){
              dispatch({type:'nextForm'});
            }
            break;
        case "PrevDot":
            dispatch({type:'prForm'});
            break;
    }
  }

  const validateForm = ()=>{
    let boxes = ['launch_box', 'presale_box', 'whitelist_box'];
    let events = {'launch_box' : 'launch', 'presale_box' : 'presale', 'whitelist_box' : 'whitelist'};
    let added_events = [];
    let added = 0
    let validated = true;
    boxes.forEach(i=>{
      if(document.getElementById(i).checked == false){
        added += 1;
      }else{
        added_events.push(events[i]);
        mustBefilled[i].forEach(f=>{
          let element = document.getElementById(f);
          if(element.value == "" || element.value == null){
            alert(f + " must be filled");
            validated = false;
            element.style.boxShadow = '0px 0px 10px red';
          }
        })
      }
    });
    if(added == 3){
      alert('please select at least 1 event');
      return false;
    }
    let cp = document.getElementById('current_phase');
    if(cp.options[cp.selectedIndex].text == "Current Phase"){
      alert('please select your current phase');
      return false;
    }
    if(!added_events.includes(cp.options[cp.selectedIndex].text.toLowerCase())){
      alert('please select your current phase');
      return false;
    }
    return validated;
  }


  const dispatch_data = ()=>{
    let vk = Object.keys(values);
    let finalData = {}
    for(let i = 0; i < vk.length; i++){
        let e = document.getElementById(vk[i]);
        if(e.type == "checkbox"){
          finalData[vk[i]] = e.checked;
        }else{
          finalData[vk[i]] = e.value;
        }
    }
    dispatch({type:'updateForm', formName : "EventInfo", value:finalData});
    return true;
  }

  return (
    <div className='EI_mainDiv'>
      <span style={{color:'blue'}}>Add As Many Events As You Want To Your Project By Checking Its Checkbox.</span>
      {
        Array(conditions.length).fill().map((_, i)=>{
          return <span style={{color:'#dcdcdc'}}><b style={{color:'blueviolet'}}>* </b>{conditions[i]}</span>
        })
      }
      <div className='EI_BoxexContainer'>
          <div className='EI_box'>
              <div className='EI_checkBox'>
                <input name='launch' type={'checkbox'} className={'CheckBoxes'} id={'launch_box'} onChange={enableDisableSections} defaultChecked={values.launch_box}></input>
                <span>Launch</span>
              </div>
              <div id='launch_section' className='EI_EventSection'>
                <div className='EI_timeInput'>
                  <span>Start:</span>
                  <input type={'datetime-local'} defaultValue={values.launch_time} id='launch_time' name='MBF'></input>
                </div>
                <input placeholder='Link' defaultValue={values.launch_link} id='launch_link'></input>
                <div className='EI_checkBox'>
                  <input type={'checkbox'} defaultChecked={values.hadPresale} id='hadPresale'></input>
                  <span>Had Presale</span>
                </div>
                <input placeholder='Presale Contribution(eg : 10000 USD)' defaultValue={values.presaleCon} id='presaleCon'></input>
                <div className='EI_smallInputs'>
                  <input placeholder='Sell Tax' defaultValue={values.sellTax} id='sellTax'></input>
                  <input placeholder='Buy Tax' defaultValue={values.buyTax} id='buyTax'></input>
                </div>
              </div>
          </div>

          <div className='EI_box'>
            <div className='EI_checkBox'>
              <input name='presale' type={'checkbox'} defaultChecked={values.presale_box} id={'presale_box'} onChange={enableDisableSections}></input>
              <span>Presale</span>
            </div>
            <div id='presale_section' className='EI_EventSection'>
              <div className='EI_timeInput'>
                <span>Start:</span>
                <input type={'datetime-local'} defaultValue={values.presale_time} id='presale_time'></input>
              </div>
              <input placeholder='Link' defaultValue={values.presale_link} id='presale_link'></input>
              <div className='EI_checkBox'>
                <input type={'checkbox'} defaultChecked={values.whitelisted} id={'whitelisted'}></input>
                <span>Whitelisted</span>
              </div>
              <div className='EI_checkBox'>
                <input type={'checkbox'} defaultChecked={values.openToPublic} id='openToPublic'></input>
                <span>Will be open to public</span>
              </div>
              <div className='EI_smallInputs'>
                <input placeholder='Soft Cap' defaultValue={values.softCap} id='softCap'></input>
                <input placeholder='Hard Cap' defaultValue={values.hardCap} id='hardCap'></input>
              </div>
            </div>
          </div>
          
          <div className='EI_box'>
            <div className='EI_checkBox'>
              <input name='whitelist' type={'checkbox'} onChange={enableDisableSections} defaultChecked={values.whitelist_box} id={'whitelist_box'}></input>
              <span>Whitelist Registration</span>
            </div>
            <div id='whitelist_section' className='EI_EventSection'>
              <div className='EI_timeInput'>
                <span>Start:</span>
                <input type={'date'} defaultValue={values.whitelist_start} id='whitelist_start'></input>
              </div>
              <div className='EI_timeInput'>
                <span>End:</span>
                <input type={'date'} defaultValue={values.whitelist_end} id='whitelist_end'></input>
              </div>
              <input placeholder='Link' defaultValue={values.whitelist_link} id='whitelist_link'></input>
            </div>
          </div>

          <div className='EI_SliderText'>
            <span id='whitelist_timeline_text'>Whitelist</span>
            <span id='presale_timeline_text'>Presale</span>
            <span id='launch_timeline_text'>Launch</span>
          </div>
          <div className='EI_SliderContainer'>
              <span className='EI_SliderLine'></span>
              <div className='EI_SliderDot'>
                <span id='whitelist_timeline_dot'></span>
                <span id='presale_timeline_dot'></span>
                <span id='launch_timeline_dot'></span>
              </div>
          </div>
      </div>

      <div className='EI_Bottom'>
        <div className='EI_Bottom_Phase'>
          <div>
              <select onChange={enableDisableSections} id='current_phase' defaultValue={values.current_phase}>
                {Array(Selects['Events'].length).fill().map((_, i)=>{
                  return <option value={Selects['Events'][i]}>{Selects['Events'][i]}</option>
                })}
              </select>
          </div>
          <div className='EI_checkBox' style={{marginTop:'20px'}}>
            <input type={'checkbox'} defaultChecked={values.cp_live} id='cp_live'></input>
            <span>Current Phase Is Live</span>
          </div>
        </div>
        <textarea className='EI_TextArea' placeholder={'write any details about this event here (like the conditions to be whitelisted or whatever)...'}
           id='eventDetails'></textarea>
        <div className='EI_Bottom_Buttons'>
          <button className='EI_Button' name='PrevDot' onClick={handleIndex}>Back</button>
          <button className='EI_Button' name='NextDot' id='EventInfo' onClick={handleIndex}>Next</button>
        </div>
      </div>
  </div>
  )
}

export default EventInfo;