import React from 'react'
import {useDispatch, useSelector, shallowEqual} from 'react-redux';
import "./Approve.css";

function Approve({values}) {
  const cf = useSelector(state=>state.cf, shallowEqual);
  const currentProject = useSelector(state=>state.form, shallowEqual);
  const projects =  useSelector(state=>state.AddProject, shallowEqual);
  const dispatch = useDispatch();

  const handleIndex = (event)=>{
    switch(event.target.name){
        case "submit":
            if(currentProject.Plans.name == "Diamond"){
              let promotedProject = {...currentProject};
              let PCount = Object.keys(projects).length;
              promotedProject["projectTag"] = "Project_" + PCount;
              dispatch({type:'promote', value:promotedProject});
            }
            if(validateForm() == true && dispatch_data()){
              dispatch({type:'AddProject', value:currentProject});
            }    
            break;
        case "PrevDot":
            dispatch({type:'prForm'})
            break;
    }
  }

  const validateForm = ()=>{
    let element = document.getElementById('username');
    if(element.value == ""){
      alert('please enter a username, we need to contact you!')
      element.style.boxShadow = '0px 0px 10px red';
      return false;
    }
    return true;
  }

  const dispatch_data = ()=>{
    let vk = Object.keys(values);
    let finalData = {}
    for(let i = 0; i < vk.length; i++){
        let e = document.getElementById(vk[i]);
        finalData[vk[i]] = e.value;
    }
    dispatch({type:'updateForm', formName : "Approve", value:finalData});
    return true;
  }
  
  return (
    <div className='Ap_mainDiv'>
        <span style={{fontSize:'40px', color:'blue'}}>
          Thanks For Choosing Our Platform {"<3"}
        </span>
        <span>Your Project Submited Successfuly And Is Currently Being Reviewd By Our Team!</span>
        <span>If EveryThing Gets Approved Your Token Will Be Shown In Tokens List In Less Than 30Minutes!</span>
        <div className='Ap_ceoLink'>
          <span>For Last Step, Give Us A Telegram ID So That We Can Contact You If Anything Goes Wrong Or Needs To Be Edited</span>
          <input name='MBF' placeholder="Owner's Telegram Username" style={{width:'25%', marginTop:'10px'}} defaultValue={values.username} id='username'></input>
        </div>
        <div className='Ap_ButtonContainer'>
          <button className='Ap_Button' name='PrevDot' onClick={handleIndex}>Back</button>
          <button className='Ap_Button' name='submit' id='Approve' onClick={handleIndex}>Submit</button>
        </div>
    </div>
  )
}

export default Approve