export const getThisFormMBFElements = (values)=>{
    let elements = [];
    let vk = Object.keys(values);
    for(let i =0; i < vk.length; i++){
        if(document.getElementById(vk[i]).name == "MBF"){
            elements.push(document.getElementById(vk[i]));
        }  
    }
    return elements;
};